import React from 'react';
import { pastproblems } from './weeklyQuestions';
import { Table } from 'react-bootstrap';
import ReactMarkdown from 'react-markdown';
import './styles.css';
export default function PastProblems() {

  return (
    <div>
      { pastproblems.map(week => {
        return (
          <div>
            <h1 className="week-in-past-problems">Week {week[0]["week"]}</h1>
            <Table striped bordered hover>
              <thead>
                <tr>
                  <th>#</th>
                  <th>Problem</th>
                  <th>Details</th>
                </tr>
              </thead>
              {week.map((problem, index) => {
                return (
                  <tbody>
                    <tr>
                      <td>{index+1}</td>
                      <td>{problem["qName"]}</td>
                      <td><ReactMarkdown source={problem["content"]}/></td>
                    </tr>
                  </tbody>
                );
              })}

            </Table>
          </div>
        );
      })}
    </div>
  )
}